import React from 'react'

const WorkersTDs = ({id, title, type, text}) => {
    let name = id.split(' ').join('-')
    console.log(name)
    return (
        <tr>
            <td>{id}</td>
            <td>{title}</td>
            <td>{type}</td>
            <td>{text}</td>
            <td><a href={'./edit_worker_'+name}>Edit</a></td>
            <td><a href={'./dismiss_worker_'+name}>Fire</a></td>
        </tr>
    )
}

export default WorkersTDs