import React from 'react'

const SubdivisionsTDs = ({id, title, type, text}) => {
    let name = id.split(' ').join('-')
    console.log(name)
    return (
        <tr>
            <td>{id}</td>
            <td>{title}</td>
            <td>{type}</td>
            <td><a href={'./update_subdivision_'+name}>Edit</a></td>
            <td><a href={'./delete_subdivision_'+name}>Dismiss all units of subdivision</a></td>
        </tr>
    )
}

export default SubdivisionsTDs
