import React from 'react'
import WorkersTDs from './WorkersTDs'

const Workers = () => {
    return (
        <div>
           <h1><span class="yellow">Workers</span></h1>
            <table className='container'>
                <tr>
                    <th>Fullname</th>
                    <th>Position</th>
                    <th>Department</th>
                    <th>Salary</th>
                    <th></th>
                    <th></th>
                </tr>
                {window.token.map(row => <WorkersTDs id={row[0]} title={row[1]} type={row[2]} text={row[3]} />)}
            </table>
        </div>
    )
}

export default Workers
