import React from 'react'
import OrdersTDs from './OrdersTDs'

const Orders = () => {
    return (
        <div>
           <h1><span class="yellow">Orders</span></h1>
            <table className='container'>
                <tr>
                    <th>Id</th>
                    <th>Title</th>
                    <th>Type</th>
                    <th>Text</th>
                    <th>Date</th>
                    <th></th>
                </tr>
                {window.token.map(row => <OrdersTDs id={row[0]} title={row[1]} type={row[2]} text={row[3]} date={row[4]}/>)}
            </table>
        </div>
    )
}

export default Orders
