import React from 'react'
import SubdivisionsTDs from './SubdivisionsTDs'

const Subdivisions = () => {
    return (
        <div>
           <h1><span class="yellow">Departments</span></h1>
            <table className='container'>
                <tr>
                    <th>Title</th>
                    <th>Positions</th>
                    <th>Unitsize</th>
                    <th></th>
                    <th></th>
                </tr>
                {window.token.map(row => <SubdivisionsTDs id={row[0]} title={row[1]} type={row[2]} />)}
            </table>
        </div>
    )
}

export default Subdivisions
