import './App.css';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import UpdateSubdivision from './elems/UpdateSubdivision'
import AddWorker from './elems/AddWorker'
import EditWorker from './elems/EditWorker'
import Subdivisions from './elems/Subdivisions'
import Orders from './elems/Orders'
import Workers from './elems/Workers'
import Header from './elems/Header'
import Main from './elems/Main'

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route exact path="/update_subdivision_:subdivision">
            <UpdateSubdivision />
            <Header />
          </Route>
          <Route exact path="/add_worker">
            <AddWorker />
            <Header />
          </Route>
          <Route exact path="/edit_worker_:fullname">
            <EditWorker />
            <Header />
          </Route>
          <Route exact path="/subdivisions">
            <Subdivisions />
            <Header />
          </Route>
          <Route exact path="/workers">
            <Workers />
            <Header />
          </Route>
          <Route exact path="/orders">
            <Orders />
            <Header />
          </Route>
          <Route exact path="/">
            <Main />
            <Header />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
