import React from 'react'

const OrdersTDs = ({id, title, type, text, date}) => {
    return (
        <tr>
            <td>{id}</td>
            <td>{title}</td>
            <td>{type}</td>
            <td>{text}</td>
            <td>{date}</td>
            <td><a href={'./download_'+id}>TO XML</a></td>
        </tr>
    )
}

export default OrdersTDs
