import {useState} from 'react'

const AddWorker = () => {
    let departments = Object.keys(window.token)
    const [fullname, setFullname] = useState('')
    const [position, setPosition] = useState('')
    const [department, setDepartment] = useState(departments[0])
    const [salary, setSalary] = useState(0)

    function Change(target){
        switch(target.name){
            case 'fullname':
                setFullname(target.value)
                break;
            case 'position':
                setPosition(target.value)
                break;
            case 'department':
                setDepartment(target.value)
                break;
            case 'salary':
                setSalary(target.value)
                break;
            default:
                break;
        }
    }
    return (
        <div class="container">
            <form method="POST" class="m-auto" style={{"max-width":"600px"}}>
                <h3 class="my-4">Worker</h3>
                <hr class="my-4" />
                <div class="form-group mb-3 row">
                    <label for="fullname2" class="col-md-5 col-form-label">Fullname</label>
                    <div class="col-md-7"><input value={fullname} onChange={(e)=>Change(e.target)} type="text" class="form-control form-control-lg" id="fullname2" name="fullname" required /> </div>
                </div>
                <div class="form-group mb-3 row">
                    <label for="department5" class="col-md-5 col-form-label">Department</label>
                    <div class="col-md-7">
                        <select value={department} onChange={(e)=>Change(e.target)} class="form-select custom-select custom-select-lg" id="department5" name="department" >
                            {departments.map(dep => <option value={dep}>{dep}</option>)}
                        </select>
                    </div>
                </div>
                <div class="form-group mb-3 row">
                    <label for="position3" class="col-md-5 col-form-label">Position</label>
                    <div class="col-md-7">
                        <select value={position } onChange={(e)=>Change(e.target)} class="form-select custom-select custom-select-lg" id="position3" name='position'>
                            {window.token[department][0].split(', ').map(pos => <option value={pos}>{pos}</option>)}
                        </select>
                    </div>
                </div>
                <div class="form-group mb-3 row"><label for="salary6" class="col-md-5 col-form-label">Salary</label>
                    <div class="col-md-7"><input value={salary} onChange={(e)=>Change(e.target)} type="text" class="form-control form-control-lg" id="salary6" name="salary" required /></div>
                </div>
                <hr class="my-4" />
                <div class="form-group mb-3 row"><label for="save8" class="col-md-5 col-form-label"></label>
                    <div class="col-md-7"><button class="btn btn-primary btn-lg" type="submit">Save</button></div>
                </div>
            </form>
        </div>
    )
}

export default AddWorker
