import {useState} from 'react'

const UpdateSubdivision = () => {
    const [title, setTitle] = useState(window.token[0])
    const [positions, setPositions] = useState(window.token[1])
    const [unitsize, setUnitsize] = useState(window.token[2])
    function Change(target){
        switch(target.name){
            case 'title':
                setTitle(target.value)
                break;
            case 'positions':
                setPositions(target.value)
                break;
            case 'unitsize':
                setUnitsize(target.value)
                break;
            default:
                break;
        }
    }
    return (
        <div class="container">
	    <form method="POST" class="m-auto" style={{"max-width":"600px"}}>
            <h3 class="my-4">Departments</h3>
            <hr class="my-4" />
            <div class="form-group mb-3 row"><label for="title2" class="col-md-5 col-form-label">Title</label>
                <div class="col-md-7"><input type="text" onChange={(e) => Change(e.target)} value={title} class="form-control form-control-lg" id="title2" name="title" required /></div>
            </div>

            <div class="form-group mb-3 row"><label for="position3" class="col-md-5 col-form-label">Positions</label>
                <div class="col-md-7"><input type="text" onChange={(e) => Change(e.target)} value={positions} class="form-control form-control-lg" id="position3" name="positions" required /></div>
            </div>

            <div class="form-group mb-3 row"><label for="unitsize4" class="col-md-5 col-form-label">Unitsize</label>
                <div class="col-md-7"><input type="text" onChange={(e) => Change(e.target)} value={unitsize} class="form-control form-control-lg" id="unitsize4" name="unitsize" required /></div>
            </div>

            <hr class="bg-transparent border-0 py-1" />
            <div class="form-group mb-3 row"><label for="send-a-message6" class="col-md-5 col-form-label"></label>
                <div class="col-md-7"><button class="btn btn-primary btn-lg" type="submit">Save</button></div>
            </div>
	</form>
</div>
    )
}

export default UpdateSubdivision
